.bg-opcan-green {
    background-color: $opcan_accent2;
}

.display-only-on-larger-screens {
    display: none;

    @include media-breakpoint-up(sm) {
        display: block;
    } 
}

.display-only-on-smaller-screens {

    @include media-breakpoint-up(sm) {
        display: none;;
    } 
}

.max-reading-width {
	max-width: $max-reading-width;
	margin-left: auto;
	margin-right: auto;
}

.date {
    @include rem(font-size, $h4-font-size);
    font-weight: $base-font-weight-heavy;
}

.container {
    padding-top: 3rem;
}

